.scroll {
  width: 100%;
  margin: auto;
  display: flex;
  flex-direction: column;
  top: 0; }
  .scroll .away {
    margin: auto;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    -webkit-animation-name: farfar;
            animation-name: farfar;
    opacity: 0;
    display: block;
    text-indent: 0;
    padding-top: 20px;
    position: absolute; }
  .scroll .title {
    font-size: 3.5em;
    font-family: "Luckiest Guy", cursive;
    letter-spacing: 0.2em;
    margin: auto;
    text-align: center;
    padding-top: 125px;
    display: block;
    position: absolute;
    width: 100%;
    opacity: 1;
    -webkit-animation-name: title;
            animation-name: title;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    -webkit-animation-duration: 25s;
            animation-duration: 25s; }
  .scroll > .galaxy {
    font-family: "Oswald", sans-serif;
    font-size: 2.5em;
    margin: auto;
    text-align: justify;
    -webkit-perspective: 400px;
            perspective: 400px; }
    .scroll > .galaxy > p {
      font-size: 1em;
      margin: auto;
      width: 80%;
      height: 50%;
      line-height: 50px;
      text-indent: 50px;
      text-align: justify;
      -webkit-animation-name: crawl;
              animation-name: crawl;
      -webkit-animation-delay: 5s;
              animation-delay: 5s;
      -webkit-animation-duration: 30s;
              animation-duration: 30s;
      opacity: 0; }

@-webkit-keyframes title {
  0% {
    opacity: 0; }
  25% {
    opacity: 0; }
  80% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes title {
  0% {
    opacity: 0; }
  25% {
    opacity: 0; }
  80% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes farfar {
  0% {
    opacity: 0; }
  25% {
    opacity: 1; }
  75% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes farfar {
  0% {
    opacity: 0; }
  25% {
    opacity: 1; }
  75% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@-webkit-keyframes crawl {
  0% {
    -webkit-transform: rotatex(30deg) translateY(300px);
            transform: rotatex(30deg) translateY(300px);
    opacity: 1; }
  25% {
    opacity: 1; }
  65% {
    opacity: 1; }
  100% {
    opacity: 1;
    -webkit-transform: rotatex(30deg) translateY(-700px);
            transform: rotatex(30deg) translateY(-700px); } }

@keyframes crawl {
  0% {
    -webkit-transform: rotatex(30deg) translateY(300px);
            transform: rotatex(30deg) translateY(300px);
    opacity: 1; }
  25% {
    opacity: 1; }
  65% {
    opacity: 1; }
  100% {
    opacity: 1;
    -webkit-transform: rotatex(30deg) translateY(-700px);
            transform: rotatex(30deg) translateY(-700px); } }

