@import './src/variables.scss';


h1 {
  font-family: $title-font;
  letter-spacing: 2px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.container {
  height: 300px;
  width: 80%;
}

main {
  display: flex;
}
